.NavBar-transparent {
    transition-duration: .5s;
    transition-timing-function: ease-out;
}

.NavBar-solid {
    background-color: rgba(41, 42, 51, 0.95);
    transition-duration: .5s;
    transition-timing-function: ease-out;
}

.NavBar-logo {
    display: inline-block;
    vertical-align: middle;
    height: 2vw;
    min-height: 30px;
    margin: 5px;

    transition-duration: 0.2s;
    transition-timing-function: ease-out;
}

.NavBar-logo:hover {
    box-shadow: #FFFFFF 0 0 20px;
}

.NavBar-navlink {
    font-size: 1.2vmax;
    font-family: Arial;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    color: white;
    transition: .2s;
    transition-timing-function: ease-out;
    border-radius: 2px;
    min-width: 10vw;
    height:100%;
}

.NavBar-navlink:hover {
    background-color: rgba(255, 255, 255, 0.3);
    color: white;
    text-decoration: none;
    text-shadow: #FFFFFF 0px 0px 5px;
}

.NavBar-lang {
    position: relative;
    top: -15px;
}

.NavBar-lang::before {
    content: url('../assets/images/navbar/i18n_icon.svg');
    position: relative;
    left:-70px;
    top: 38px;
    width: 10px;
    height: 10px;
}

.page {
    width: 100%;
}

.page-enter {
    opacity: 0;
}

.page-enter-active {
    opacity: 1;
    transition: opacity 500ms;
}

.page-exit {
    opacity: 1;
}

.page-exit-active {
    opacity: 0;
    transition: opacity 500ms;
}